@import "reset";
@import "fonts";
@import "vars";
html,
body {
    height: 100%;
}

.wrapper {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    // overflow-x: hidden;
    font-family: $font-primary;
    font-size: 15px;
}

.container {
    max-width: 1080px;
    height: 100%;
    margin: 0 auto;
    padding-left: 1%;
    padding-right: 1%;
    box-sizing: border-box;
}

#container {
    overflow: hidden;
}

.pol1,
.pol2,
.pol3,
.pol4,
.pol5 {
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: absolute;
}

.pol1 {
    width: 35px;
    height: 35px;
    background-image: url(../img/pol-1.png);
}

.pol2 {
    width: 27px;
    height: 27px;
    background-image: url(../img/pol-2.png);
}

.pol3 {
    width: 35px;
    height: 35px;
    background-image: url(../img/pol-3.png);
}

.pol4 {
    width: 10px;
    height: 10px;
    background-image: url(../img/pol-4.png);
}

.pol5 {
    width: 25px;
    height: 25px;
    background-image: url(../img/pol-5.png);
}

sup {
    opacity: 0.6;
    font-size: 8px;
    vertical-align: super;
    a {
        color: inherit;
    }
}

.header {
    position: relative;
    padding-top: 25px;
    padding-bottom: 20px;
    min-height: 128px;
    box-sizing: border-box;
    background-color: $green;
    background-image: url("../img/header-bg.png"), url("../img/shadow-down.png");
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-size: 100% 100%, 100% 100%;
    &:after {
        content: "";
        position: absolute;
        bottom: -17px;
        left: 11%;
        width: 45%;
        height: 31px;
        background: url("../img/shine.png") no-repeat 100% 100%;
        z-index: 10;
    }
    &__title-box {
        position: relative;
        width: 350px;
        height: 80px;
        margin: 0 auto;
        padding-top: 3px;
        background-image: url("../img/text_box.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        z-index: 15;
        &:after {
            content: "";
            position: absolute;
            bottom: -10px;
            left: 0px;
            width: 350px;
            height: 15px;
            opacity: 0.7;
            background: url("../img/title-shade.png") no-repeat 100% 100%;
        }
    }
    @media only screen and (max-width: 368px) {
        &__title-box {
            width: 100%;
            height: auto;
            &:after {
                display: none;
            }
        }
    }
    &__title {
        font-family: 'futuramedium';
        font-size: 30px;
        text-align: center;
        line-height: 1.2;
        a {
            color: #fff;
        }
    }
    &__pollen {
        width: 100%;
        // max-width: 400px;
        height: 128px;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
    }
}

.menu-wrap {
    position: relative;
    background-color: $green;
    width: 100%;
    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 15px;
        background-image: url("../img/shadow-down.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        z-index: 9;
        transform: rotate(180deg);
    }
}

.menu {
    min-height: 70px;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    &__item {
        position: relative;
        margin-top: 5px;
        margin-bottom: 5px;
        font-family: 'futuralight';
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 40px;
            width: 1px;
            background-image: url("../img/spacer.png");
            background-size: contain;
        }
    }
    .menu__item--wide {
        padding-left: 10px;
        padding-right: 10px;
    }
    .menu__item--last {
        &:after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            height: 40px;
            width: 1px;
            background-image: url("../img/spacer.png");
        }
    }
    .is-active {
        color: #01453e;
    }
    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        min-width: 115px;
        min-height: 40px;
        line-height: 1.2;
        color: #fff;
        transition: color 0.3s ease;
        &:hover {
            color: #01453e;
        }
    }
    &__dropdown {
        visibility: hidden;
        height: 0px;
        position: absolute;
        width: 150px;
        padding-top: 15px;
        opacity: 0;
        background-color: $green;
        transition: all .3s ease-in-out;
    }
    .menu__item:hover .menu__dropdown {
        height: 150px;
        opacity: 1;
        visibility: visible;
        display: flex;
        flex-direction: column;
        z-index: 1000;
        .menu__item--dropdown {
            opacity: 1;
            transition-delay: 0.3s;
            transition: opacity .2s ease-in-out;
        }
    }
    .menu__item--dropdown {
        display: flex;
        padding: 10px 10px;
        color: #fff;
        opacity: 0;
        border-bottom: 1px solid #ccc;
        &:hover {
            background-color: darken($green, 10%)
        }
    }
}

.fluid-container {
    margin: 0 auto;
}

.owl-carousel {
    overflow: hidden;
}

.owl-slide {
    position: relative;
    // img {
    //     max-height: 450px;
    //     width: auto;
    // }
    p {
        position: absolute;
        top: 8%;
        left: 0;
        width: 100%;
        text-align: center;
        font-family: "futuralight";
        font-size: 56px;
        color: #58595b;
        @media only screen and (max-width: 1200px) {
            font-size: calc( 16px + (46 - 16) * ((100vw - 400px) / ( 1200 - 400)));
        }
        span {
            font-family: "futuramedium";
            font-weight: 500;
        }
    }
}

// MAIN CONTENT
main {
    flex: 1 0 auto;
    position: relative;
}

.owl-item {
    -webkit-box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
    box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
}

.cat-bottom {
    position: absolute;
    height: 240px;
    bottom: -3px;
    right: 15%;
    img {
        height: 100%;
    }
}

@media only screen and (max-width: 1600px) {
    .cat-bottom {
        right: 2%;
    }
}

@media only screen and (max-width: 1366px) {
    .cat-bottom {
        right: 0%;
    }
}

//FOOTER
.footer {
    position: relative;
    flex: 0 0 auto;
    min-height: 90px;
    padding: 10px;
    margin-bottom: 10px;
    background-color: $green;
    -webkit-box-shadow: 0px 10px 0px 0px #fba51a;
    -moz-box-shadow: 0px 10px 0px 0px #fba51a;
    box-shadow: 0px 10px 0px 0px #fba51a;
    border-bottom: 6px solid #dcdb88;
    .container {
        position: relative;
    }
    &__icons-bar {
        text-align: center;
        margin: 0 auto;
    }
    @media only screen and (max-width: 368px) {
        &__icons-bar {
            img {
                width: 100%;
            }
        }
    }
    &__references {
        position: absolute;
        right: 20px;
        bottom: 10px;
        font-family: 'advent-Lt1', Calibri, sans-serif;
        letter-spacing: 0.5px;
        color: #fff;
        transition: color 0.3s ease;
        &:hover {
            color: #01453e;
        }
    }
    &__keeping {
        position: absolute;
        right: 5%;
        top: 5px;
        width: 180px;
        height: 40px;
        padding-top: 3px;
        font-size: 18px;
        font-family: 'futuramedium';
        line-height: 1.1;
        background-image: url("../img/text_box.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        &:after {
            content: "";
            position: absolute;
            bottom: -10px;
            left: 0px;
            width: 180px;
            height: 15px;
            opacity: 0.7;
            background: url("../img/title-shade--small.png") no-repeat 100% 100%;
        }
    }
    .footer__title {
        text-align: center;
        color: #fff;
    }
}

@media only screen and (max-width: 768px) {
    .footer {
        text-align: center;
        .footer__keeping {
            position: static;
            margin: 0 auto;
            margin-top: 10px;
            &:after {
                display: none;
            }
        }
        .footer__references {
            position: static;
            margin: 0 auto;
        }
    }
}

// PAGE: WHAT ARE ALLERGY
.content {
    height: 100%;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    .content__title {
        margin-top: 50px;
        font-family: "futuramedium";
        font-size: 36px;
        color: $green;
        span {
            font-family: Arial;
        }
    }
    p {
        margin-top: 20px;
        margin-bottom: 10px;
        line-height: 1.5;
        font-weight: 300;
        color: #58595b;
    }
    .content__list {
        margin-left: 15px;
        list-style-type: disc;
    }
    .content__list-item {
        line-height: 1.5;
        font-weight: 300;
        color: #58595b;
    }
    .content__tips-link {
        padding: 2px 5px;
        font-size: 12px;
        font-weight: 300;
        color: #fff;
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }
    .content__cat-bottom {
        position: absolute;
        bottom: -3px;
        right: 3%;
    }
}

// PAGE: WHAT ARE ALLERGY
.content--what {
    box-sizing: border-box;
    .content__text {
        max-width: 40%;
    }
    &__cat {
        margin-right: 20px;
        align-self: flex-end;
    }
    &__woman {
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
    }
}

@media only screen and (max-width: 1000px) {
    .content--what {
        .content {
            justify-content: center;
        }
        .content__text {
            max-width: 60%;
        }
    }
    .content--what__woman {
        display: none;
    }
}

@media only screen and (max-width: 700px) {
    .content--what {
        text-align: center;
        .content--what__cat {
            order: 2;
        }
        .content__text {
            max-width: 100%;
        }
    }
}

// PAGE: Allergy Symptoms
.content--symptoms {
    justify-content: space-around;
    .symptoms__text {
        margin-left: 6%;
    }
    .symptoms__title {
        margin-top: 30px;
        margin-bottom: 40px;
        font-size: 38px;
        font-family: "futuralight";
        color: #58595b;
    }
    .symptoms__img {
        width: 50%;
        img {
            width: 100%;
        }
    }
}

// PAGE: ALLERGY TRIGGERS
.triggers {
    position: relative;
    width: 100%;
    &:before {
        position: absolute;
        content: "";
        height: 50px;
        width: 70%;
        top: 60.4%;
        background-color: rgba(0, 0, 0, 0.2);
        z-index: -1;
    }
}

.content--triggers {
    flex-direction: column;
    .content__title {
        margin-top: 30px;
        margin-bottom: 25px;
        font-family: "futuralight";
    }
    .triggers__text {
        padding-left: 30px;
    }
    p {
        margin-top: 10px;
        margin: 0;
    }
    .triggers__allergens {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding-bottom: 30px;
    }
    .triggers__picture-box {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        // &:before {
        //     position: absolute;
        //     content: "";
        //     height: 50px;
        //     width: 100%;
        //     top: 36%;
        //     background-color: rgba(0, 0, 0, 0.2);
        //     z-index: 0;
        // }
    }
    .triggers__picture {
        width: 123px;
        height: 123px;
        margin: 15px 15px 15px 0;
        border: 15px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        z-index: 10;
        &:first-child {
            margin-left: 30px;
        }
        &:last-child {
            margin-right: 80px;
        }
        img {
            width: 100%;
            height: 100%;
            border: 1px solid #fff;
            border-radius: 50%;
        }
    }
    .triggers__list {
        margin-bottom: 10px;
        padding: 10px;
        padding-bottom: 30px;
        border: 10px solid rgba(0, 0, 0, 0.2);
        background-color: #fff;
        z-index: 10;
        li {
            list-style-type: disc;
        }
    }
    .triggers__allergens-title {
        margin-top: 20px;
        margin-bottom: 20px;
        font-size: 22px;
        font-weight: 500;
        color: #f99a05;
        sup {
            font-weight: 300;
            font-size: 12px;
        }
    }
    .triggers__allergen-item {
        margin-left: 30px;
        line-height: 1.5;
        font-size: 18px;
        font-weight: 300;
        color: #58595b;
    }
}

@media only screen and (max-width: 875px) {
    .triggers {
        &:before {
            display: none;
        }
        .triggers__allergens {
            justify-content: center;
        }
        .triggers__picture-box {
            order: 2;
            &:before {
                display: none;
            }
            .triggers__picture {
                margin-left: 15px;
                margin-right: 15px;
            }
        }
        .triggers__list {
            order: 1;
            margin-top: 15px;
        }
    }
}

// PAGE: TYPES OF ALLERGY
.content--types {
    justify-content: center;
    flex-wrap: wrap;
    padding-bottom: 15px;
    // -webkit-box-shadow: inset 0px 40px 44px -30px rgba(0, 0, 0, 0.75);
    // -moz-box-shadow: inset 0px 40px 44px -30px rgba(0, 0, 0, 0.75);
    // box-shadow: inset 0px 40px 44px -30px rgba(0, 0, 0, 0.75);
    .types__text {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
    .list-box {
        max-width: 290px;
    }
    .content__list-title {
        margin-bottom: 15px;
        color: $green;
    }
    .content__list-item {
        margin-left: 10px;
        padding-left: 5px;
    }
    .list-box--center {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 40px;
        margin-right: 60px;
        background-repeat: no-repeat;
        background-size: cover;
        .content__list-title {
            margin-top: 45px;
            padding-left: 15px;
            padding-right: 15px;
            line-height: 1.2;
            font-size: 20px;
            color: #58595b;
            font-weight: 500;
            text-align: center;
        }
    }
    .types__cat-point-box {
        margin-top: 20px;
        text-align: center;
    }
    .types__bottom-title {
        font-size: 20px;
        font-weight: 500;
        color: $green;
    }
    @media only screen and (max-width: 730px) {
        .types__text {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        .list-box {
            max-width: 400px;
        }
        .list-box--center {
            padding-left: 20px;
            padding-right: 20px;
            order: 1;
            margin-left: 0;
            margin-right: 0;
        }
        .list-box--left {
            order: 2;
            margin-top: 20px;
        }
        .list-box--right {
            order: 3;
            margin-top: 20px;
        }
    }
}

// PAGE: SKIN ALLERGIES
.content--skin {
    padding-bottom: 70px;
    padding-left: 40px;
    .skin__title {
        margin-top: 45px;
        font-size: 24px;
        font-weight: 500;
        color: $green;
    }
    .skin__desc {
        max-width: 85%;
    }
    .skin__desc--last {
        max-width: 80%;
    }
    @media only screen and (max-width: 1100px) {
        padding-bottom: 175px;
    }
    @media only screen and (max-width: 650px) {
        padding-bottom: 225px;
        padding-left: 0;
        .skin__desc {
            max-width: 100%;
        }
    }
}

//PAGE: ANTIHISTAMINES
.content--anti {
    padding-top: 35px;
    padding-left: 40px;
    padding-bottom: 70px;
    .anti__title {
        margin-top: 25px;
        margin-bottom: 25px;
        font-size: 24px;
        color: #f99a05;
        font-weight: 500;
    }
    .anti__list-title {
        margin-bottom: 5px;
        font-weight: 400;
        color: #000;
    }
    .anti__number-line--1 {
        margin-top: 5px;
        margin-bottom: 5px;
    }
    .anti__line--orange {
        color: #f99a05;
        margin-bottom: 5px;
    }
    .content__list-item {
        margin-left: 3px;
        padding-left: 5px;
    }
    .anti__number-line--2 {
        max-width: 75%;
    }
    @media only screen and (max-width: 1100px) {
        padding-bottom: 125px;
    }
    @media only screen and (max-width: 650px) {
        padding-bottom: 225px;
        padding-left: 0;
        .anti__title {
            text-align: center;
        }
    }
}

// PAGE: TIPS
.content--tips {
    padding: 25px 0 85px 40px;
    .tips__text {
        display: flex;
        justify-content: space-between;
    }
    .tips__column1 {
        width: 55%;
        padding-right: 10%;
        box-sizing: border-box;
    }
    .tips__spacer {
        height: 330px;
        align-self: center;
    }
    .tips__column2 {
        width: 45%;
        padding-left: 5%;
        padding-top: 7%;
        box-sizing: border-box;
    }
    .tips__title {
        margin-bottom: 25px;
        font-size: 24px;
        font-family: "futuralight";
        color: #58595b;
        font-weight: 700;
    }
    .tips__subtitle {
        margin-bottom: 15px;
        line-height: 1.5;
        font-size: 16px;
        font-weight: 700;
        color: $green;
    }
    .content__list-item {
        span {
            font-weight: 500;
        }
        p {
            margin-top: 0;
            font-weight: 300;
        }
    }
    .content__tips-link {
        margin-left: 10px;
    }
    .skin-allergy-img {
        position: absolute;
        top: 0;
        right: 0;
    }
    @media only screen and (max-width: 650px) {
        padding: 25px 0 225px 0;
        .tips__title {
            text-align: center;
        }
        .skin-allergy-img {
            display: none;
        }
        .tips__text {
            flex-direction: column;
        }
        .tips__column1,
        .tips__column2 {
            width: 100%;
            padding: 0;
        }
        .tips__spacer {
            display: none;
        }
    }
}

//  TIPS SKIN
.skin {
    .skin-allergy-img {
        position: absolute;
        top: 0;
        right: 0;
    }
    @media only screen and (max-width: 1000px) {
        .skin-allergy-img {
            display: none;
        }
    }
    @media only screen and (max-width: 500px) {
        padding-bottom: 50px;
    }
}

.tips__skin {
    padding-bottom: 90px;
    .tips__text {
        flex-direction: column;
    }
    .tips__title {
        font-size: 30px;
        font-family: 'futuralight';
        color: #58595b;
        margin: 20px 0 30px 5px;
    }
    .tips__subtitle {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 10px;
    }
    .content__list-item {
        margin-bottom: 20px;
    }
    @media only screen and (max-width: 800px) {
        padding-bottom: 175px;
    }
}

// PAGE: REFERENCE 
.content--ref {
    .ref__text {
        padding-left: 40px;
        padding-bottom: 60px;
        font-size: 13px;
        color: #58595B;
    }
    .ref__logo {
        max-width: 144px;
        margin-top: 25px;
        margin-bottom: 25px;
    }
    .ref__subtitle {
        margin-bottom: 15px;
        font-family: "futuramedium";
        a {
            color: inherit;
        }
    }
    .ref__list-item {
        margin-bottom: 10px;
        font-family: "futuralight";
    }
    .reference__number {
        margin-right: 2px;
        font-family: "futuramedium";
        font-weight: 700;
    }
    @media only screen and (max-width: 1000px) {
        .ref__text {
            padding-left: 0;
            font-size: 15px;
        }
    }
}

// PAGE: CONTACT
.contact {
    padding-top: 30px;
    padding-bottom: 40px;
    .contact__title {
        margin-bottom: 30px;
        font-size: 30px;
        color: #f99a05;
        text-align: center;
        text-transform: uppercase;
    }
}

.form__group {
    background-color: rgba(0, 0, 0, 0.15);
    margin: 0 auto;
    padding: 40px 60px 20px 60px;
    border-radius: 5px;
    max-width: 360px;
    .form__elem {
        width: 100%;
        margin-bottom: 20px;
    }
    input,
    textarea {
        width: 100%;
        font-family: inherit;
        font-size: 16px;
        border: 1px solid lighten(#58595b, 30%);
        padding-left: 15px;
        border-radius: 3px;
        box-sizing: border-box;
        &:focus {
            border-color: transparent;
            outline-color: $green;
        }
    }
    input {
        height: 40px;
    }
    .form__btn {
        width: 100%;
        margin: 0 auto;
        background-color: $green;
        color: #fff;
        text-transform: uppercase;
        border-color: transparent;
        &:hover {
            cursor: pointer;
            background-color: darken($green, 10%);
        }
    }
}

@media only screen and (max-width: 1000px) {
